.rtl{
    direction:rtl;
    unicode-bidi:embed;

    //- Elements
    select{
        background-position: 20px center;
        padding-left: 60px;
        padding-right: 20px;
    }
    .text-left{ text-align: right }
    .text-right{ text-align: left }

    .pull-left{ float: right }
    .pull-right{ float: left }

    .section-header:after{
        left: auto;
        right: 0;
    }
    .section-header--center:after{
        left: 0;
        right: 0;
        margin: auto;
    }
    .section-header--left:after{
        left: 0;
        right: auto;
        margin: auto;
    }
    .hdr-widget:not(:last-child):after{
        right: auto;
        left: 0;
    }

    //- Menu Toggle
    .hdr-widget--menu-main .menu-main-toggle{
        padding-left: 20px;
        padding-right: 0;
    }
    
    //- Header Logo
    .hdr-widget--site-logo{
        border-right: none;
        padding-left: 20px;
    }

    //- Search Box
    .hdr-widget--product-search .product-search-input { padding-right: 20px }
    .hdr-widget--product-search .product-search-input input{
        padding-left: 40px;
        padding-right: 20px;
        text-align: right
    }
    .hdr-widget--product-search .fa,
    .hdr-widget--product-search .product-search-category .fa{
        right: auto;
        left: 0;
    }
    .hdr-widget--product-search .product-search-category select{
        padding-left: 40px;
        padding-right: 0;
    }
    .hdr-widget--product-search .product-search-input label{
        right: 40px;
    }

    //- Dropdown menu: User & Cart
    .hdr-widget--menu-user .menu-user-wrap,
    .hdr-widget--menu-cart .widget_shopping_cart{
        left: 0;
        right: auto;
    }
    .hdr-widget--menu-cart{
        @media screen and (max-width: 768px){
            margin-right: auto;
            margin-left: 0;
        }
    }
    //- Dropdown arrow
    .hdr-widget--menu-user .menu-user-wrap:before,
    .hdr-widget--menu-user .menu-user-wrap:after{
        right: auto;
        left: 20px;
    }

    //- Main Menu Position
    .menu-background .menu-main-background,
    .menu-main-wrapper{
        left: auto;
        right: 0;
    }
    .menu-main-wrapper:before,
    .menu-main-wrapper:after{
        left: auto;
        right: 20px;
    }
    .menu-background .menu-main-background.sub-open{
        box-shadow: -4px 0 5px -4px rgba(0, 0, 0, 0.1);
    }
    //- Menu has children arrow
    .menu-main-wrapper .menu > .menu-item.menu-item-has-children > a:after{
        right: auto;
        left: 10px;
        transform: rotate(180deg);
    }
    .menu-main-wrapper .menu-item .fa, .menu-main-wrapper .menu-item [class*="ico"]{
        float: right;
        margin-left: 10px;
        margin-right: 0;
    }
    .menu-main-wrapper .menu-item.mega-menu > .sub-menu,
    .menu-main-wrapper .menu-item:not(.mega-menu) .sub-menu{
        left: auto;
        right: 100%;
    }

    .menu-main-wrapper .menu-item.mega-menu > .sub-menu .sub-menu{
        left: auto;
        right: 0;
    }

    .menu-background .sub-bg-container{
        background-position: bottom left;
        left: 0;
    }

    //- Mobile Menu
    .mobile-menu .open-sub{
        left: 0;
        right: auto;
    }
    .mobile-menu .menu-item a .fa, .mobile-menu .menu-item a [class*="ico"], .mobile-menu .menu-item a [class*="ti-"]{
        float: right;
        margin-right: 0;
        margin-left: 10px;
        margin-top: 5px;
    }
    .mobile-menu .menu .sub-menu .menu-item{
        border-left: none;
        border-right: 1px solid #e3e3e3;
    }

    .gallery-slider{
        direction: ltr;
    }

    //- Posts navigation
    .posts-navigation .next-link{ float: left }
    .posts-navigation .prev-link{ float: right }
    .posts-navigation {
        .fa{
            transform: rotate(180deg);
        }
    }

    .comment-body{
        padding-right: 80px;
        padding-left: 0;
    }
    .comment-meta .avatar{
        right: 0;
        left: auto;
    }

    //- Post navigation
    .post__nav .post__nav--prev{
        text-align: right;
        border-right: 0;
        border-left: 1px solid #e3e3e3;

        &:before{ 
            right: 0;
            left: auto;
            transform: rotate(180deg);
        }
    }
    .post__nav .post__nav--next{
        text-align: left;
        &:before{ 
            left: 0;
            right: auto;
            transform: rotate(180deg);
        }
    }
    .post__meta{
        padding-right: 70px;
        padding-left: 0;

        &:before{
            right: 0;
            left: auto;
        }
    }
    @media screen and (min-width: 991px) {
        .woocommerce-Reviews > div:first-child {
            padding-right: 15px;
            padding-left: 30px;
            border-left: 1px dashed #e0e0e0;
            border-right: none;
        }
        .woocommerce-Reviews > div:first-child + div{
            padding-right: 30px;
            padding-left: 15px;
        }
    }
    .woocommerce-Reviews .comment_container{
        padding-right: 80px;
        padding-left: 0;
    }
    .woocommerce-Reviews .comment .avatar{
        right: 0;
        left: auto;
    }
    @media screen and (min-width: 640px){
        .woocommerce-Reviews .comment .star-rating{
            left: 0;
            right: auto;
        }

    }

    //- Product
    .star-rating{
        text-align: right;
        &:before{
            right: 0;
            left: auto;
        }
        span{
            left: auto;
            text-align: right;
            right: 0;
            &:before{
                left: auto;
                right: 0;
            }
        }
    }
    .product-overview .cart:not(.variations_form) .input-text.qty{
        margin-right: 0;
        margin-left: 20px;
    }

    .product-list .product__detail{
        padding-left: 0;
        padding-right: 40px;
    }
    .product-list .product__price{
        float: right;
    }
    .product-list .product__action{
        float: left;
    }

    .product-overview__image, .product-overview__summary,
    #yith-quick-view-content div.images{
        float: right;
    }

    .book-images .book__action button [class*="ico"], .book-images .book__action .see-inside [class*="ico"]{
        margin-right: 0;
        margin-left: 10px;
    }
    .product-overview__summary{
        padding-left: 0;
        padding-right: 50px;

        .product__meta .author {
            float: right;
        }
        .product__meta .woocommerce-product-rating{
            float: left;
        }
        .product-deals-info .deal-countdown, .product-overview__summary .product-deals-info .deal-saving{
            float: right;
        }
        .product-deals-info .deal-countdown{
            border-left: 1px solid #e0e0e0;
            border-right: none;
        }
    }

    .variations_form.cart .single_variation_wrap .woocommerce-variation-add-to-cart .quantity{
        margin-left: 20px;
        margin-right: 0;
    }

    .tokoo-look-inside .look-inside-box .look-inside-book-detail .book-item .book-detail{
        padding-left: 0;
        padding-right: 20px;
    }
    .tokoo-look-inside .look-inside-box .look-inside-book-title button{
        left: 0;
        right: auto;
    }
    .product-overview__summary .onsale{
        right: auto;
        left: 0;
    }
    .product-overview__summary .product-deals-info .deal-saving .saving:not(:last-child){
        border-right: none;
        border-left: 1px solid #e0e0e0;
    }
    .col2-set .col-1{float: right}
    .col2-set .col-2{float: left}
    .woocommerce-checkout > h3, .woocommerce-checkout .woocommerce-checkout-review-order{
        float: left;
    }
    .woocommerce-checkout .col2-set{
        float: right;
    }
    .wc_payment_methods.payment_methods .wc_payment_method label{
        padding-right: 60px;
        padding-left: 20px;

        &:before{
            right: 20px;
            left: auto;
        }
        &:after{
            right: 25px;
            left: auto;
        }
    }
    .wc_payment_methods.payment_methods .wc_payment_method label img{
        float: left;
    }
    .variations_form.cart .single_variation_wrap .woocommerce-variation.single_variation .woocommerce-variation-price{
        float: right;
    }
    .product-overview__summary .product-bookmark .post__share a .fa{
        margin-right: 0;
        margin-left: 10px;
    }
    .select2-container .select2-choice {
        padding: 5px 20px;
    }
    .woocommerce-info, .woocommerce-message{
        padding: 30px 80px 30px 30px
    }
    .woocommerce-info:before, .woocommerce-message:before{
        right: 30px;
        left: auto;
    }
    .product-layout-view a{
        margin-right: 0;
        margin-left: 10px;
    }
    .widget.widget_shopping_cart li{
        padding-right: 0;
        padding-left: 40px;
    }
    .widget.widget_shopping_cart .remove{
        left: 0;
        right: auto;
    }
    .widget_search form input[type="text"], .widget_search form input[type="search"]{
        padding-left: 100px;
        padding-right: 20px;
    }

    .site-footer__widget-area .grid-item:not(:last-child){
        border-right: none;
        border-left: 1px dashed #e0e0e0;
    }
    .shop_table.cart .actions .coupon{
        float: right;
    }
    .shop_table.cart .actions > .button{
        float: left;
    }
    .shop_table.cart th.product-name,
    .shop_table.cart .product-name{
        text-align: right;
    }
    p.demo_store:before{
        margin-right: 0;
        margin-left: 20px;
    }
    #shipping_method label{
        padding-right: 40px;
        padding-left: 0;
        text-align: right;

        &:before{
            left: auto;
            right: 0;
        }
        &:after{
            left: auto;
            right: 4px;
        }
    }

    //- Widgets
    .widget_search form input[type="submit"]{
        right: auto;
        left: 0;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        border-top-left-radius: 50px;
        border-bottom-left-radius: 50px;
    }
    .widget.woocommerce-top-product .product_list_widget li{
        padding-left: 0;
        padding-right: 20px;

        &:before{
            left: auto;
            right: 0;
        }
    }
    .widget.woocommerce-best-selling-product .product_list_widget li:before{
        left: auto;
        right: -20px;
    }
    .product_list_widget .product-image{
        margin-right: 0;
        margin-left: 10px;
    }
    .widget.widget_rating_filter ul .star-rating{
        float: right;
        margin-top: 5px;
    }
    .widget.tokoo-mailchimp-subscribe-form .mc4wp-form-fields input[type="email"], .widget.tokoo-mailchimp-subscribe-form .mc4wp-form-fields input[type="text"]{
        padding-left: 100px;
        padding-right: 20px;
    }
    .widget.tokoo-mailchimp-subscribe-form .mc4wp-form-fields input[type="submit"]{
        left: 0;
        right: auto;
    }

    //- KC Elements
    .kc-pro-button a{
        white-space: nowrap;
    }
    .kc-feature-boxes.kc-fb-layout-3 {
        direction: ltr;
    }
    .category-icon .grid-item.category-icon__cat{
        padding: 15px 80px 15px 15px;
    }
    .category-icon__cat-image{
        right: 0;
        left: auto;
    }

    .woocommerce-MyAccount-navigation-link a:before {margin-left: 10px; margin-right: 0 !important;}
    .woocommerce-MyAccount-content .woocommerce-Message--info .woocommerce-Button {float: left !important;}

    .woocommerce-message .button{
        float: left;
    }
    
    .mobile-menu-wrap .hdr-widget--menu-user .menu-user-avatar{
        text-align: right;
        img{
            margin-right: 0;
            margin-left: 10px;
        }
        &:after{
            right: auto;
            left: 20px;
        }
    }

    #yith-quick-view-close{
        left: 10px;
        right: auto;
    }
    .yith-wcqv-main{
        .product-overview__summary{
            text-align: right;

        } 
        
        .images{
            padding-left: 30px;
            padding-right: 0;
        }
    }
    #yith-quick-view-content .onsale{
        right: 40px;
        top: 40px;
        left: auto;
        z-index: 2;
    }
    .hdr-widget--menu-cart .widget_shopping_cart:before{
        left: 30px;
        right: auto;
    }
    .hdr-widget--menu-cart .widget_shopping_cart:after{
        left: 28px;
        right: auto;
    }
}
